<template>
  <div id="app">
        <b-navbar toggleable="lg" type="dark" variant="info">
          <b-navbar-brand href="#">
            <img src="../static/images/logo.svg" class="d-inline-block align-top" alt="Kitten">

          </b-navbar-brand>

          <b-navbar-toggle target="nav-collapse"></b-navbar-toggle>

          <b-collapse id="nav-collapse" is-nav>

            <!-- Right aligned nav items -->
            <b-navbar-nav class="ml-auto">
              <router-link to="/">主页</router-link>
              <router-link to="/category">专栏</router-link>

              <router-link to="/feed">关注</router-link>
              <router-link to="/hot">热门</router-link>
              <router-link to="/search">查询作者</router-link>
              <router-link to="/collection">收藏</router-link>
              <router-link to="/autovote">自动点赞</router-link>
              <router-link to="/comments">评论</router-link>
              <router-link to="/reply">回复</router-link>

              <router-link to="/post">发布</router-link>


              <div v-if="this.$store.state.username">
                <a href="#">@{{this.$store.state.username}}</a>
                <a href="#" @click="logout">注销</a>
              </div>
              <div v-else >

                <a href="https://signup.steemit.com" target="_blank">注册</a>
                <router-link to="/login">登录</router-link>
              </div>


            </b-navbar-nav>
          </b-collapse>
        </b-navbar>

        <router-view/>

    <!--<Test></Test>-->

    <Footer></Footer>

  </div>
</template>

<script>
  import Footer from './components/Footer'
  import Category from './components/Category'
  import Reply from './components/Reply'
  import Test from './components/Test'
 export default {
  name: 'App',
  data(){
    return {
      username:'',
      password:'',
      keywords: '',

    }
  },
  methods: {
    login(){
      let that = this
      this.steem.api.getAccounts([that.username], function(err, result) {
        //最好在这之前检查下用户名是否存在，否则这个函数无法执行！
        let pubWif = result[0].posting.key_auths[0][0]
        // console.log(111, pubWif)
        let privWif = that.password
        let isvalid
        try{
          isvalid = that.steem.auth.wifIsValid(privWif, pubWif);
          // console.log(123, isvalid)
        }
        catch(e){
          isvalid = 'false'
        }

        if(isvalid === true){
          console.log(8888, ' Welcome.')
          that.$store.commit('saveUser', {username: that.username, password: privWif})
          that.$router.push({path:'/index'})

        }else{
          alert(`<h1>请检查用户名和发贴密钥！</h1>`)
        }
      })
    },

    logout(){
      this.$store.commit('clearUser')
    },

  },

  filters:{
    formatTime(data){
      return data.substring(0,10)
    }
  },

  components: {
    Footer,
    Category,
    Reply,
    Test,
   },

   mounted() {
     if(localStorage.getItem("autostart") !=null){
       console.log(222, '开始点赞啦！')
       let authors = JSON.parse(localStorage.getItem("authors"))
       this.getDataAndVote(authors)
     }

   }
 }
</script>

<style>
  html,body{
    height: 100%;
  }
  #app{
    position:relative;
    width:100%;
    min-height:100%;
    padding-bottom: 4rem;
    box-sizing: border-box;
  }

  .router-link-exact-active{
    color: darkcyan !important;
    border-bottom: 1px solid darkcyan;
    text-align: center;
  }


  .fade-enter,
  .fade-leave-active {
    opacity: 0;
  }
  .fade-enter-active,
  .fade-leave-active {
    transition: opacity 0.5s;
  }

  img {
    vertical-align: middle;
    width: 85% !important;
    background-color: blueviolet;
  }

  .bg-info {
    background-color: #da741b !important;

  }
  .navbar-brand img {
    margin-left: 2rem;
    background-color: transparent;
  }


  #nav-collapse{
    margin-right: 4rem;
  }
  #nav-collapse a{
    color: white;
    margin-right: 0.4rem;
    margin-left: 0.4rem;



  }
  #nav-collapse a:hover{
    color: darkcyan;
    text-decoration: none;
  }


</style>



