<template>
    <div>
      这是测试

    </div>

</template>

<script>
    export default {
        name: "Test",
      data(){
          return {
            str:'',
            tag:'',
            newposts:[],
            date:'',
            replies:[],
            newreplies:[],
            all:[]


          }
      },

      methods:{
        getDataAndVote(authors) {
          let acc = this.$store.state.username
          console.log(111, acc)
          console.log(122, authors)
          let key =this.$store.state.password

          let that = this
          this.steem.api.streamTransactions("head", function (err, result) {
            // console.log(234, err, result)
            if (err) {
              alert("网络有问题！请更换API节点!")
            };
            let txType = result.operations[0][0]  //获取操作类别
            let txData = result.operations[0][1]  //获取操作内容

            authors.forEach(item => {
              if (txType == "comment" & txData.author == item.author & txData.parent_author == "") {//获取作者的文章
                let permlink = txData.permlink
                let voteweight = item.weight
                let vote = that.steem.broadcast.vote( key, acc, item.author, permlink, voteweight*100, function(err, result) {
                  console.log(456, err, result)
                  console.log(688, "点赞完成！")
                })
                setTimeout(vote, 1000*60)  //60秒后
              }
            })
          })
        }
      },
      mounted() {
        let _this = this
        let author = 'lemooljiang'

        let replies = []
        async function main(){
          //从steem上获取最新评论的回复
          let query = { limit : 20, start_author:'lemooljiang'}
          let comments = await  _this.steem.api.getDiscussionsByCommentsAsync(query)
          console.log(234, comments)
          for(let item of comments){
            if(item.children != 0){
              //如果有子评论就往下查
              let newreplies = await _this.steem.api.getContentRepliesAsync(author, item.permlink)
              console.log(345, newreplies)
              replies = replies.concat(newreplies)
            }

          }

          //从steem上获取最新文章的回复
          let beforeDate = new Date().toISOString().split('.')[0]
          let startPermlink = null
          let results = await _this.steem.api.getDiscussionsByAuthorBeforeDateAsync(author, startPermlink, beforeDate, 10)
          console.log(444, results)
          for(let item2 of results){
            let newreplies2 = await _this.steem.api.getContentRepliesAsync(author, item2.permlink)
            console.log(456, newreplies2)
            replies = replies.concat(newreplies2)
          }

          //对所有回复进行时间排序，最新的在前
          replies.sort(function(a, b){
            return a.created < b.created  ? 1 : -1
          })

          console.log(678, replies)

        }
        // main()


        async function getSteemReplies(){
          let res = await _this.steem.api.getAccountHistoryAsync(author, -1, 2000)
          res.forEach(item => {
            if(item[1].op[0] == "comment" & item[1].op[1].parent_author == "lemooljiang"){
              replies.push(item)
            }
          })
          console.log(566, replies)
        }

        getSteemReplies()

        // let i = "scisan"
        // let p = "q19zu1"
        // this.steem.api.getContent(i, p, function(err, result) {
        //   // _this.post = result
        //   console.log(567, err, result)
        // })


      }


    }
</script>

<style scoped>
  .second{
    margin-left: 1rem;
  }

  .third{
    margin-left: 2rem;
  }

</style>



